// src/app/api/cart/remove/[id]/route.ts

import { NextRequest, NextResponse } from "next/server";
import prisma from "../../../../../../prisma/client"
import { withAuth } from "../../../../../middleware/authMiddleware";

export async function DELETE(req: NextRequest, { params }: { params: { id: any , userId: any} }) {
  try {
    const sessionToken = req.cookies.get("session-us")?.value; // Retrieve session-us cookie

    if (!sessionToken) {
      return NextResponse.json(
        { message: "Not authenticated", isLoggedIn: false },
        { status: 401 }
      );
    }

    let user = await prisma.user.findFirst({
      where: {
        sessions: {
          some: {
            sessionToken: sessionToken,
          },
        },
      },
    });

    if (!user) {
      return NextResponse.json(
        { message: "You are not logged in!", isLoggedIn: false },
        { status: 401 }
      );
    }
    const { id, userId } = params; // Get cartItemId from URL parameters

    const cartItem = await prisma.cartItem.findUnique({
      where: { productId: Number(id), userId: userId },
    });

    if (!cartItem || cartItem.userId !== user.userId) {
      return NextResponse.json(
        { message: "Cart item not found or unauthorized", success: false },
        { status: 404 }
      );
    }

    await prisma.cartItem.delete({
      where: { productId: Number(id), userId: userId },
    });

    return NextResponse.json(
      { message: "Item removed from cart successfully", success: true },
      { status: 200 }
    );
  } catch (error) {
    if(error instanceof Error)
    {
      console.log(error.stack)
    }
    console.error("Error removing item from cart:", error);
    return NextResponse.json(
      { message: "Failed to remove item from cart", success: false },
      { status: 500 }
    );
  }
}

export const config = {
  handler: withAuth,
};
